body {
  background-color: #e8e8e8;
  margin: 0;
  padding: 0;
}

body {
  font-family: "Montserrat", sans-serif;
}

h1 {
  text-align: center;
  font-size: 55px;
  margin-top: 120px;
}

p {
  font-size: 15px;
  text-align: center;
  max-width: 80%;
  margin: 0 auto;
}

#buttons {
  margin: 20px;
  text-align: center;
}

button {
  margin-top: 30px;
  width: 200px;
  background-color: blue;
  color: white;
  padding: 15px 30px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
  transition: background-color 0.3s ease;
}

button:hover {
  background-color: #45a049;
}

.gallery-img {
  width: 100%;
  cursor: pointer;
  transition: transform 0.2s;
}

.gallery-img:hover {
  transform: scale(1.05);
}

#lightbox {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

#destinationDisplay {
  margin: 20px auto;
  text-align: center;
  padding: 20px;
  background-color: #f0f0f0;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  font-size: 18px;
  font-weight: bold;
}

#destinationDisplay h2 {
  text-align: center;
  font-size: 38px;
  margin-bottom: 10px;
}

#destinationDisplay p {
  margin-bottom: 10px;
  font-size: 16px;
}

#destinationDisplay img {
  width: 100%;
}

@media screen and (min-width: 1200px) {
  h1 {
    text-align: center;
    font-size: 100px;
    margin-top: 150px;
  }
  p {
    font-size: 20px;
    text-align: center;
  }
  #buttons {
    display: flex;
    justify-content: center;
    gap: 100px;
    margin: 20px;
  }
  button {
    margin-top: 50px;
    width: 200px;
    background-color: blue;
    color: white;
    padding: 15px 30px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 16px;
    transition: background-color 0.3s ease;
  }
  button:hover {
    background-color: #45a049;
  }
  .gallery {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    justify-content: center;
  }
  .gallery-img {
    width: 100%;
    cursor: pointer;
    transition: transform 0.2s;
  }
  .gallery-img:hover {
    transform: scale(1.05);
  }
  #lightbox {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.8);
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
  }
  #destinationDisplay {
    margin: 20px auto;
    text-align: center;
    padding: 20px;
    background-color: #f0f0f0;
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    font-size: 18px;
    font-weight: bold;
  }
  #destinationDisplay h2 {
    color: #333; /* Title color */
    font-size: 50px;
    margin-bottom: 10px;
  }
  #destinationDisplay p {
    color: #666;
    margin-bottom: 20px;
    font-size: 20px;
    font-weight: 200;
  }
  #destinationDisplay img {
    width: 20%;
  }
}